<template>
  <div id="app">
    <!-- 插件基本结构 -->
    <div id="container" data-XSwitch>
      <div class="sections">
        <!-- 第1屏----------------------------------- -->
        <div class="section" id="section0">
          <div class="header">
            <ul>
              <!-- <li><img class="base front" src="" alt=""><a href="javascript:;" class="base top">glad to see you</a></li>
              <li><img class="base front" src="" alt=""><a href="javascript:;" class="base top">glad to see you</a></li> -->
              <li><a class="front" href="sprouts.html" >SPROUTS</a><a href="sprouts.html" class="top">豆芽</a></li>
              <li><a class="front" href="pop.html">POPROCKS</a><a href="pop.html" class="top">跳跳糖</a></li>
              <li><a class="front" href="tree.html">TREE</a><a href="tree.html" class="top">特立独行的树</a></li>
              <li><a class="front" href="perfume.html">PERFUME</a><a href="perfume.html" class="top">香水百合</a></li>
            </ul>
          </div>
          <div class="main">
            <div class="desc">
              <p> welcome to the space! </p>
              <p> Here a magical journey is about to begin! </p>
            </div>
            <div class="pic">
              <img src="../../assets/img/pearl.jpg" alt="">
            </div>
          </div>
          
        </div>
        <!-- 第2屏--------------------------------------------- -->
        <div class="section" id="section1">
          <div class="wrap">
            <div class="part junior" :class="{style: mask}" @mouseover="colorChange(0)" @mouseout="colorChange(1)" @click="getPage('sprouts.html')">
            </div>
            <div class="part senior" :class="{style: mask2}" @mouseover="colorChange2(0)" @mouseout="colorChange2(1)" @click="getPage('pop.html')">

            </div>

            <div class="part college" :class="{style: mask3}" @mouseover="colorChange3(0)" @mouseout="colorChange3(1)" @click="getPage('tree.html')">

            </div>
            <div class="part lover" :class="{style: mask4}" @mouseover="colorChange4(0)" @mouseout="colorChange4(1)" @click="getPage('perfume.html')">

            </div>
          </div>
        </div>
        <!-- 第3屏 ---------------------------------- -->
        <div class="section" id="section2">
          <!-- 按钮 -->
          <button><span>shake</span></button>
        </div>
        <!-- 第4屏----------------------------------- -->
        <div class="section" id="section3">
          shanghai
        </div>

      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return{
      mask: false,
      mask2: false,
      mask3: false,
      mask4: false,
    }
  },
  methods: {
    colorChange(index) {
      if (index == 0) {
        this.mask = true;
      } else {
        this.mask = false;
      }    
    },
    colorChange2(index) {
      if (index == 0) {
        this.mask2 = true;
      } else {
        this.mask2 = false;
      }      
    },
    colorChange3(index) {
      if (index == 0) {
        this.mask3 = true;
      } else {
        this.mask3 = false;
      }      
    },
    colorChange4(index) {
      if (index == 0) {
        this.mask4 = true;
      } else {
        this.mask4 = false;
      }      
    },
    getPage(url){
      if (url) {
        window.location.href = url;
      }      
    }
  }
}
</script>

<style media="screen" lang="less">
body,ol,ul,h1,h2,h3,h4,h5,h6,p,th,td,dl,dd,form,fieldset,legend,input,textarea,select {
  margin: 0;
  padding: 0
}
// 第一屏样式
#section0{
  .header{
    width: 100%;   
    height: 80px; 
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(34, 5, 5);
    li{
      list-style: none;
      width: 200px;
      height: 80px;
      float: left;
      position: relative;
      transform-style: preserve-3d;
      transition: 1s;
      a{              
        text-decoration: none;
        width: 200px;
        text-align: center;
        height: 80px;
        line-height: 80px;
        display: block;
        color: #fff; 
        position: absolute;        
        left: 0;
        top: 0;
        
      } 
      .front{
        background-color: #ffc400;
        transform: translateZ(40px);
        transition: .6s;
      }
      .top{
        background-color: #1d89ff;
        transform: rotateX(90deg) translateZ(40px);
        transition: .5s;
      } 
      &:hover .front{
        transform: rotateX(-90deg) translateZ(40px);
        transition: .5s;
      }
      &:hover .top{
        transform: translateZ(40px);
        transition: .6s;
      }       
    }     
  }
  .main{
    width: 1000px;
    // height: 600px;
    margin: 42px auto;
    .desc {
      text-align: center;
      font: 700 18px "微软雅黑";
      text-shadow: 4px 2px 4px #bbb;    
      margin-bottom: 20px;  
    }
    .pic {
      width: 600px;
      padding: 10px;
      margin: auto;
      overflow: hidden;
      box-shadow: 0 0 8px #ccc;
      img {
        width: 600px;
        transition: all 0.6s;
      }
    }
    .pic:hover img {
      transition: all 0.6s;
      transform: scale(1.1);
    }
  }
}
// 第二屏样式
#section1{
  body,html{
    width: 100%;
    height: 100%;
  }
  .wrap{
    width: 80%;
    height: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    .part{
    width: 30%;
    height: 40%;
    margin: 4%;
    border-radius: 0 0 20% 0;
    box-shadow: 6px 6px 6px #ccc;
    background-color: #ffc400;
    // background-color: rgb(216, 203, 187);
    position: relative;
    }
    .style{
      background-color: rgba(243, 160, 51, 0.6);
    }
    .part:before{
      display: block;
      content: '';
      width: 12px;
      height: 12px;
      border: 16px solid rgb(58, 54, 41);
      border-radius: 50%;
      position: absolute;
      left: -10px;
      top: -10px;
      transform: rotateX(20deg) rotateY(20deg);
    }
    // 第二屏动画
    @keyframes leftTop {
      0% {
        left: -600px;
        top: -300px;
      }
      100% {
        left: 0px;
        top: 0px;
      }
    }
    @-moz-keyframes leftTop {
       0% {
        left: -600px;
        top: -300px;
      }
      100% {
        left: 0px;
        top: 0px;
      }
    }
    @-webkit-keyframes leftTop {
       0% {
        left: -600px;
        top: -300px;
      }
      100% {
        left: 0px;
        top: 0px;
      }
    }
    @-o-keyframes leftTop {
       0% {
        left: -600px;
        top: -300px;
      }
      100% {
        left: 0px;
        top: 0px;
      }
    }
    // senior部分
    @keyframes rightTop {
      0% {
        right: -600px;
        top: -300px;
      }
      100% {
        right: 0px;
        top: 0px;
      }
    }
    // college部分
    @keyframes leftBottom {
      0% {
        left: -400px;
        bottom: -300px;
      }
      100% {
        left: 0px;
        bottom: 0px;
      }
    }
    // lover部分
    @keyframes rightBottom {
      0% {
        right: -400px;
        bottom: -300px;
      }
      100% {
        right: 0px;
        bottom: 0px;
      }
    }
    
  }
}
// 第三屏样式
#section2{
  button {
    position: relative;
    height: 40px;
    width: 100px;
    margin: 10px;
    outline: none;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    text-transform: uppercase;
    background-color: #ffffff;
    border: 1px solid #ffc400;
    border-radius: 10px;
    color: #ffc400;
    font-weight: 400;
    font-size: 20px;
    font-family: inherit;
    z-index: 0;
    overflow: hidden;
    transition: all 0.3s cubic-bezier(0.02, 0.01, 0.47, 1);
    span {
      font: 700 16px '微软雅黑';
      letter-spacing: px;
    }
    &:hover {
      animation: rotate 0.7s ease-in-out both;
    }
    &:hover span {
      animation: storm 0.7s ease-in-out both;
      animation-delay: 0.06s;
    }  
    @keyframes rotate {
      0% {
        transform: rotate(0deg) translate3d(0, 0, 0);
      }
      25% {
        transform: rotate(3deg) translate3d(0, 0, 0);
      }
      50% {
        transform: rotate(-3deg) translate3d(0, 0, 0);
      }
      75% {
        transform: rotate(1deg) translate3d(0, 0, 0);
      }
      100% {
        transform: rotate(0deg) translate3d(0, 0, 0);
      }
    }
    @keyframes storm {
      0% {
        transform: translate3d(0, 0, 0) translateZ(0);
      }
      25% {
        transform: translate3d(4px, 0, 0) translateZ(0);
      }
      50% {
        transform: translate3d(-3px, 0, 0) translateZ(0);
      }
      75% {
        transform: translate3d(2px, 0, 0) translateZ(0);
      }
      100% {
        transform: translate3d(0, 0, 0) translateZ(0);
      }
    }
    &:before, &:after {
      position: absolute;
      right: 0;
      bottom: 0;
      width: 100px;
      height: 100px;
      border-radius: 50%;
      background: #ffc400;
      content: '';
      opacity: 0;
      transition: transform 0.15s cubic-bezier(0.02, 0.01, 0.47, 1), opacity 0.15s cubic-bezier(0.02, 0.01, 0.47, 1);
      z-index: -1;
      transform: translate(100%, -25%) translate3d(0, 0, 0);
    }
    &:hover:before, &:hover:after {
      opacity: 0.15;
      transition: transform 0.2s cubic-bezier(0.02, 0.01, 0.47, 1), opacity 0.2s cubic-bezier(0.02, 0.01, 0.47, 1);
    }
    &:hover:before {
      transform: translate3d(50%, 0, 0) scale(0.9);
    }
    &:hover:after {
      transform: translate(50%, 0) scale(1.1);
    }     
  }

}

#app {
  height: 100%;
  overflow: hidden;
}
/*插件 简单reset*/
* {
  margin: 0;
  padding: 0;
}
/*必须，关系到单个page能否全屏显示*/
html,
body {
  height: 100%;
  overflow: hidden;
}
#container,
.sections,
.section {
  /*必须，兼容，在浏览器不支持transform属性时，通变top/left完成滑动动画*/
  position: relative;
  /*必须，关系到单个page能否全屏显示*/
  height: 100%;
}
.section {
  /*有背景图时必须，关系到背景图能够全屏显示*/
  background-color: #fff;
  background-size: cover;
  background-position: 50% 50%;
}
/*以下样式用来设置slider样式，可自行修改*/
.pages {
  position: fixed;
  right: 20px;
  top: 50%;
  list-style: none;
}
.pages li {
  width: 6px;
  height: 8px;
  background-color: #ffc400;
  border-radius: 50% 10%;
  margin: 15px 0 0 7px;
}
.pages li.active {
  margin-left: 4px;
  background-color: #fff;
  border: 4px solid #ffc400;
  transition: all 1s ease;
}
</style>



